.productdisplay{
    display:flex;
    width:1200px;
    /* margin:0px 170px; */
    margin:0 auto

}
.productdisplay-left{
    flex:1;
    display:flex;
    gap:17px
}
.productdisplay-img-list{
    display:Flex;
    flex-direction: column;
    gap:10px
}
.productdisplay-img-list img{
    width:120px;
    height:auto;
    border:1px solid #ddd
}
.productdisplay-main-img{
 width:800px;
 /* height:700px */
}
.productdisplay-right{
    flex:1;
    margin:0px 70px;
    display:flex;
    flex-direction: column;
    text-align:left
}
.productdisplay-right h1{
    color:#3d3d3d;
    font-size:30px;
    font-weight:normal;
    line-height:1.2
}
.productdisplay-right-star{
    display:flex;
    align-items: center;
    margin-top:13px;
    gap:5px;
    color:#1c1c1c;
    font-size:16px
}
.productdisplay-right-price{
    display:flex;
    margin:40px 0;
    gap:30px;
    font-size:24px;
    font-weight:700
}
.productdisplay-right-price-old{
color:#818181;
text-decoration:line-through
}
.productdisplay-right-price-new{
color:#ff4141
}
.productdisplay-right-sizes h1{
margin-top:55px;
font-size:20px;
color:#656565;
font-weight:600
}
.productdisplay-right-size{
    display:Flex;
    margin:30px 0;
    gap:20px;
}
.productdisplay-right-size div{
    padding:18px 24px;
    background:#fbfbfb;
    border:1px solid #ddd;
    border-radius:3px;
    cursor:pointer

}
.productdisplay-right button{
    padding:20px 40px;
    width:200px;
    font-size:16px;
    font-weight:600;
    color:#fff;
    background:#ff4141;
    margin-bottom:40px;
    border:none;
    outline:none;
    cursor:pointer
}
.productdisplay-right-category{
    margin-top:10px;
}
.productdisplay-right-category span{
font-weight:600
}
